import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faColumns } from '@fortawesome/free-solid-svg-icons';

import "./app.css";

import { TEMPLATE_NEW_RENTAL_DISCOUNT, TEMPLATE_NEW_STEPPED_RENT } from '../templates/newColumns';
import { DATA_SHEET_NAME, DATA_TABLE_NAME } from '../constants';
export default class ColumnManager extends React.Component {
  constructor(props, context) {
    super(props, context);
  }

  /**
   * Add Rental Discount Columns
   */
  _createNewRentalDiscounts = async () => {
    console.log('create new rental discounts');
    try {
      await Excel.run(async context => {
        var sheet = context.workbook.worksheets.getActiveWorksheet();
        var table = sheet.tables.getItem(DATA_TABLE_NAME);

        var headerRange = table.getHeaderRowRange().load("values");

        var bodyRange = table.getDataBodyRange().load("values");
        var numberFormat = table.getDataBodyRange().load('numberFormat');

        var selectedRange = context.workbook.getSelectedRange().load("columnIndex");

        return context.sync()
          .then(function () {
            let formalCount = 0;
            headerRange.values[0].forEach(elem => {
              if (elem.includes(TEMPLATE_NEW_RENTAL_DISCOUNT.prefix)) {
                formalCount++;
              }
            })
            formalCount = Math.ceil(formalCount / TEMPLATE_NEW_RENTAL_DISCOUNT.header.length) + 1;

            var bodyValues = bodyRange.values;

            numberFormat = numberFormat.numberFormat[0]
            const rowCount = bodyValues.length;
            let header = [];
            TEMPLATE_NEW_RENTAL_DISCOUNT.header.forEach(elem => {
              header.push(
                TEMPLATE_NEW_RENTAL_DISCOUNT.prefix + formalCount + (elem != "" ? (" " + elem) : "")
              )
            })

            let indexToAdd = selectedRange.columnIndex;
            numberFormat.splice(indexToAdd, 0, ...TEMPLATE_NEW_RENTAL_DISCOUNT.format);
            console.log(indexToAdd, numberFormat)

            header.forEach(elem => {
              let dataToAdd = [
                [elem],
              ]
              for (var i = 0; i < rowCount; i++) {
                dataToAdd.push([""]);
              }
              table.columns.add(indexToAdd++, dataToAdd)
            })
            for (var i in numberFormat) {
              table.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = numberFormat[parseInt(i)];
            }
            return context.sync()
          });
      });
    } catch (error) {
      console.error(error);
    }
  }

  /**
   * Add new Stepped Rent Columns
   */
  _createNewSteppedRent = async () => {
    console.log('create new stepped rent');
    try {
      await Excel.run(async context => {
        var sheet = context.workbook.worksheets.getActiveWorksheet();
        var table = sheet.tables.getItem(DATA_TABLE_NAME);

        var headerRange = table.getHeaderRowRange().load("values");

        var bodyRange = table.getDataBodyRange().load("values");
        var numberFormat = table.getDataBodyRange().load('numberFormat');

        var selectedRange = context.workbook.getSelectedRange().load("columnIndex");

        return context.sync()
          .then(function () {
            let formalCount = 0;
            headerRange.values[0].forEach(elem => {
              if (elem.includes(TEMPLATE_NEW_STEPPED_RENT.prefix)) {
                formalCount++;
              }
            })
            formalCount = Math.ceil(formalCount / TEMPLATE_NEW_STEPPED_RENT.header.length) + 1;

            var bodyValues = bodyRange.values;
            numberFormat = numberFormat.numberFormat[0]

            const rowCount = bodyValues.length;
            let header = [];
            TEMPLATE_NEW_STEPPED_RENT.header.forEach(elem => {
              header.push(
                TEMPLATE_NEW_STEPPED_RENT.prefix + formalCount + (elem != "" ? (" " + elem) : "")
              )
            })

            let indexToAdd = selectedRange.columnIndex;
            numberFormat.splice(indexToAdd, 0, ...TEMPLATE_NEW_STEPPED_RENT.format);

            header.forEach(elem => {
              let dataToAdd = [
                [elem],
              ]
              for (var i = 0; i < rowCount; i++) {
                dataToAdd.push([""]);
              }
              table.columns.add(indexToAdd++, dataToAdd)
            })
            for (var i in numberFormat) {
              table.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = numberFormat[parseInt(i)];
            }
            return context.sync();
          });
      });
    } catch (error) {
      console.error(error);
    }
  }

  render() {
    const {
      isOfficeInitialized,
    } = this.props;

    if (!isOfficeInitialized) {
      return (
        <div></div>
      );
    }

    return (
      <div className="section">
        <p className="section-title">Add Columns</p>
        <div className="button-container">
          <div className="button" onClick={this._createNewRentalDiscounts}>
            <FontAwesomeIcon className="icon" icon={faColumns} />
            <span>Rental Discounts</span>
          </div>
          <div className="button" onClick={this._createNewSteppedRent}>
            <FontAwesomeIcon className="icon" icon={faColumns} />
            <span>Stepped Rent</span>
          </div>
        </div>
      </div>
    );
  }
}
