import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFile, faFileAlt } from '@fortawesome/free-solid-svg-icons'

import "./app.css";

import { TEMPLATE_SIMPLE } from '../templates/template-simple';
import { TEMPLATE_FULL } from '../templates/template-full';
import { STRING, NUMBER, DATE, CURRENCY, BOOLEAN, PERCENT, FUNCTION } from '../templates/fieldTypes';

import { DATA_SHEET_NAME, DATA_TABLE_NAME } from '../constants';

export default class TemplateManager extends React.Component {
  constructor(props, context) {
    super(props, context);
  }

  _createSimpleTemplate = async () => {
    try {
      await Excel.run(async context => {
        console.log("Create Simple Template")
        var currentWorksheet = context.workbook.worksheets.getActiveWorksheet();
        currentWorksheet.name = DATA_SHEET_NAME;

        var tbSimpleTemplate = currentWorksheet.tables.add("A1:N1", true /*hasHeaders*/);
        tbSimpleTemplate.name = DATA_TABLE_NAME;

        // set header
        let header = [];
        TEMPLATE_SIMPLE.header.forEach(col => {
          header.push(col.title)
        })
        tbSimpleTemplate.getHeaderRowRange().values = [header];
        tbSimpleTemplate.getHeaderRowRange().format.wrapText = true;
        tbSimpleTemplate.getHeaderRowRange().format.autofitRows();
        // end set header

        // set values
        TEMPLATE_SIMPLE.fields.forEach(values => {
          tbSimpleTemplate.rows.add(null, [values]);
        })
        for (var i in TEMPLATE_SIMPLE.header) {
          let type = TEMPLATE_SIMPLE.header[i].type;
          switch (type) {
            case STRING:
              tbSimpleTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = '';
              break;
            case NUMBER:
              tbSimpleTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = 'Number';
              break;
            case DATE:
              tbSimpleTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = 'dd.mm.yy';
              // console.log(tbFullTemplate.columns.getItemAt(i).getDataBodyRange())
              break;
            case PERCENT:
              tbSimpleTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = '#%';
              break;
            case BOOLEAN:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().dataValidation.rule = {
                list: {
                  inCellDropDown: true,
                  source: "Yes,No"
                }
              };
              break;
            default:
              break;
          }
        }
        // end set values

        await context.sync();
      });
    } catch (error) {
      console.error(error);
    }
  }

  _createFullTemplate = async () => {
    try {
      await Excel.run(async context => {
        var currentWorksheet = context.workbook.worksheets.getActiveWorksheet();
        currentWorksheet.name = DATA_SHEET_NAME;

        var tbFullTemplate = currentWorksheet.tables.add("A1:AI1", true /*hasHeaders*/);
        tbFullTemplate.name = DATA_TABLE_NAME;

        // set header
        let header = [];
        TEMPLATE_FULL.header.forEach(col => {
          header.push(col.title)
        })
        tbFullTemplate.getHeaderRowRange().values = [header];
        tbFullTemplate.getHeaderRowRange().format.wrapText = true;
        tbFullTemplate.getHeaderRowRange().format.autofitRows();
        // end set header

        // set values
        TEMPLATE_FULL.fields.forEach(values => {
          tbFullTemplate.rows.add(null, [values]);
        })

        for (var i in TEMPLATE_FULL.header) {
          let type = TEMPLATE_FULL.header[i].type;
          console.log(i, type);
          switch (type) {
            case STRING:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = '';
              break;
            case NUMBER:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = 'Number';
              break;
            case DATE:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = 'dd.mm.yy';
              break;
            case PERCENT:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().numberFormat = '#%';
              break;
            case BOOLEAN:
              tbFullTemplate.columns.getItemAt(parseInt(i)).getDataBodyRange().dataValidation.rule = {
                list: {
                  inCellDropDown: true,
                  source: "Yes,No"
                }
              };
              break;
            default:
              break;
          }
        }
        // tbFullTemplate.columns.getItemAt(1).getDataBodyRange().numberFormat = "dd.mm.yy";
        // tbFullTemplate.columns.getItemAt(2).getDataBodyRange().numberFormat = "dd.mm.yy";
        // end set values

        return context.sync();
      });
    } catch (error) {
      console.error(error);
    }
  }

  render() {
    const { isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return (
        <div></div>
      );
    }

    return (
      <div className="section">
        <p className="section-title">Create Template</p>
        <div className="button-container">
          <div className="button" onClick={this._createSimpleTemplate}>
            <FontAwesomeIcon className="icon" icon={faFile} />
            <span>Simple Template</span>
          </div>
          <div className="button" onClick={this._createFullTemplate}>
            <FontAwesomeIcon className="icon" icon={faFileAlt} />
            <span>Full Template</span>
          </div>
        </div>
      </div>
    );
  }
}
